// ----------------------------------------------------------------------------
//  Dependencies
// ----------------------------------------------------------------------------
import React, {useState} from 'react';
import styled from 'styled-components'

// ----------------------------------------------------------------------------
//  Styles
// ----------------------------------------------------------------------------
const AnswerBox = styled.div`
	border: 2px solid black;
	display: flex;
	flex-direction: column;
	height: 50px;
	width: 196px;
`

const Button = styled.button`
	background: none;
	font-size: 20px;
	height: 50px;
	width: 50px;

	&:hover {
		background: #d3d3d3;
	}
`

const ClearButton = styled.button`
	background: #ffcccb;
	font-size: 15px;
	height: 50px;
	width: 100px;

	&:hover {
		background: #ff0000;
	}
`

const EqualsButton = styled.button`
	background: #00FFFF;
	font-size: 20px;
	height: 50px;
	width: 100px;

	&:hover {
		background: #1E90FF
	}
`

const ErrorMessage = styled.span`
	font-size: 12px;
	text-align: right;
`

const Input = styled.span`
	font-size: 25px;
	text-align: right;
`

const NegativePositiveButton = styled(Button)`

`

const Row = styled.div`
	display: flex;
	flex-direction: row;
	justify-content: space-between;
`

const Wrapper = styled.div`
	display: flex;
	flex-direction: column;
	height: 300px;
	margin: auto;
	width: 200px;
`

// ----------------------------------------------------------------------------
//  Components
// ----------------------------------------------------------------------------

const Calculator = () => {
	const [display, setDisplay] = useState(0);
	const [firstNumber, setFirstNumber] = useState(null);
	const [firstNumberIn, setFirstNumberIn] = useState(false)
	const [operator, setOperator] = useState(null);
	const [operatorIn, setOperatorIn] = useState(false);
	const [secondNumber, setSecondNumber] = useState(null);
	const [secondNumberIn, setSecondNumberIn] = useState(false);
	const [decimalIn, setDecimalIn] = useState(false);
	const [isNegative, setIsNegative] = useState(false);
	const [error, setError] = useState('')

	const NumberClick = (value) => {
		setError('')
		if (firstNumberIn === false && firstNumber === null) {
			setFirstNumber(value);
			setDisplay(value);
		} else if (firstNumberIn === false && firstNumber.length > 0) {
			setFirstNumber(firstNumber + value);
			setDisplay(firstNumber + value);
		} else if (firstNumberIn === true && operatorIn === true && secondNumber === null) {
			setSecondNumber(value);
			setDisplay(value);
		} else if (firstNumberIn === true && operatorIn === true && secondNumber.length > 0) {
			setSecondNumber(secondNumber + value);
			setDisplay(display + value);
		}
	}

	const NegativePositiveFlip = () => {
		if (isNegative === false) {
			if (firstNumber !== null && secondNumber === null && firstNumber > 0) {
				setFirstNumber('-' + firstNumber);
				setDisplay('-' + display);
				setIsNegative(true);
			} else if (operatorIn === true && secondNumber !== null) {
				setSecondNumber('-' + secondNumber)
				setDisplay('-' + secondNumber)
				setIsNegative(true);
			} 
		} else if (isNegative === true) {
			setError('Number is already negative')
		}
	}

	const DecimalClick = () => {
		if (decimalIn === true) {
			setError('You may only use one decimal')
		} else if (firstNumberIn === false) {
			setFirstNumber(firstNumber + '.');
			setDisplay(firstNumber + '.')
			setDecimalIn(true);
		} else if (firstNumberIn === true && operatorIn === true) {
			setSecondNumber(secondNumber + '.');
			setDisplay(display + '.');
			setDecimalIn(true);
		}
	}
	
	const OperatorClick = (value) => {
		setError('');
		setDecimalIn(false);
		setIsNegative(false);
		if (firstNumber === null) {
			setError('Enter a number')
		}
			else if (firstNumberIn === false && operatorIn === false) {
			setFirstNumberIn(true);
			setOperatorIn(true);
			setOperator(value);
			setDisplay(value)
		} else if (operatorIn === true && secondNumber === null && firstNumberIn === true) {
			setOperator(value);
			setDisplay(value);
		}
	}

	const EqualsClick = () => {
		setError('');
		setDecimalIn(false);
		if (operator === '+' && firstNumberIn === true && secondNumber !== null) {
			setDisplay((Number(firstNumber) + Number(secondNumber)).toString());
			setFirstNumber((Number(firstNumber) + Number(secondNumber)).toString());
			setFirstNumberIn(false);
			setOperator(null);
			setOperatorIn(false);
			setSecondNumber(null);
		} else if (operator === '-' && firstNumberIn === true && secondNumber !== null) {
			setDisplay((Number(firstNumber) - Number(secondNumber)).toString());
			setFirstNumber((Number(firstNumber) - Number(secondNumber)).toString());
			setFirstNumberIn(false);
			setOperator(null);
			setOperatorIn(false);
			setSecondNumber(null);
		} else if (operator === 'x' && firstNumberIn === true && secondNumber !== null) {
			setDisplay((Number(firstNumber) * Number(secondNumber)).toString());
			setFirstNumber((Number(firstNumber) * Number(secondNumber)).toString());
			setFirstNumberIn(false);
			setOperator(null);
			setOperatorIn(false);
			setSecondNumber(null);
		} else if (operator === '/' && firstNumberIn === true && secondNumber !== null) {
			setDisplay((Number(firstNumber) / Number(secondNumber)).toString());
			setFirstNumber((Number(firstNumber) / Number(secondNumber)).toString());
			setFirstNumberIn(false);
			setOperator(null);
			setOperatorIn(false);
			setSecondNumber(null);
		}
			
		
	}

	const clearFunction = () => {
		setDisplay(0);
		setFirstNumberIn(false);
		setFirstNumber(null);
		setOperator(null);
		setOperatorIn(false);
		setSecondNumber(null);
		setSecondNumberIn(false);
		setDecimalIn(false);
		setError('');
		setIsNegative(false);
	}
	return (
		<Wrapper>
			<AnswerBox>
				<Input>{display}</Input>
				<ErrorMessage>{error}</ErrorMessage>
			</AnswerBox>
			<Row>
				<Button onClick={() => NumberClick('7')}>7</Button>
				<Button onClick={() => NumberClick('8')}>8</Button>
				<Button onClick={() => NumberClick('9')}>9</Button>
				<Button onClick={() => OperatorClick('+')}>+</Button>
			</Row>
			<Row>
				<Button onClick={() => NumberClick('4')}>4</Button>
				<Button onClick={() => NumberClick('5')}>5</Button>
				<Button onClick={() => NumberClick('6')}>6</Button>
				<Button onClick={() => OperatorClick('-')}>-</Button>
			</Row>
			<Row>
				<Button onClick={() => NumberClick('1')}>1</Button>
				<Button onClick={() => NumberClick('2')}>2</Button>
				<Button onClick={() => NumberClick('3')}>3</Button>
				<Button onClick={() => OperatorClick('x')}>&times;</Button>
			</Row>
			<Row>
				<Button onClick={() => NumberClick('0')}>0</Button>
				<Button onClick={DecimalClick}>.</Button>
				<NegativePositiveButton onClick={NegativePositiveFlip}>+/-</NegativePositiveButton>
				<Button onClick={() => OperatorClick('/')}>&divide;</Button>
			</Row>
			<Row>
				<ClearButton onClick={clearFunction}>AC</ClearButton>
				<EqualsButton onClick={EqualsClick}>=</EqualsButton>
			</Row>
		</Wrapper>
	)
}

export default Calculator